<script>
import DealDataService from "../../../services/DataService";
import crmtimeline from "../../../components/Common/Modal/crmtimeline.vue";
import crmnote from "../../../components/Common/Modal/crmnote.vue";
import crmattachment from "../../../components/Common/Modal/crmattachment.vue";
import crmsendemail from "../../../components/Common/Modal/crmsendemail.vue";
import crmcontacttab from "../../../components/Common/Modal/crmcontacttab.vue";
import crmproduct from "../../../components/Common/Modal/crmproduct.vue";
import crmcall from "../../../components/Common/Modal/crmcall.vue";
import modalattachment from "../../../components/Common/Modal/modalattachment.vue"
import modaltask from "../../../components/Common/Modal/modaltask.vue";
import modalevent from "../../../components/Common/Modal/modalevent.vue";
import modalemail from "../../../components/Common/Modal/modalemail.vue";
import modalexistingcontact from "../../../components/Common/Modal/modalexistingcontact.vue";
import addadditionalowner from '../../../components/Common/Modal/addadditionalowner.vue'
import modalcall from "../../../components/Common/Modal/modalcall.vue";
import modalcontact from "../../../components/Common/Modal/modalcontact.vue";
import modalproduct from "../../../components/Common/Modal/modalproduct.vue";
import crmreadmore from "../../../components/Common/Modal/crm-readmore.vue";
export default {
  components: {
    crmtimeline,
    crmnote,
    crmattachment,
    crmsendemail,
    crmcontacttab,
    crmproduct,
    crmcall,
    modalattachment,
    modaltask,
    modalevent,
    modalemail,
    addadditionalowner,
    modalexistingcontact,
    modalcall,
    modalcontact,
    modalproduct,
    crmreadmore
  },
  data() {
    return {
      activeKey: 1,
      isNotes: false,
      isQuotation: false,
      isActivity: false,
      isAttachment: false,
      isSendEmail: false,
      isContact: false,
      isProduct: false,
      isOwner: false,
      isDetailPage: false,
      iscontactDetails: null,
      showExistingContactModal: false,
      showContactModal: false,
      showConvertDeal: false,
      showProductModal: false,
      showAddTaskModal: false,
      showAddEventModal: false,
      showCallModal: false,
      sendEmailModal: false,
      showAttachmentModal: false,
      LeftViewData: [],
      IsShow: true,
      noteData: [],
      attachmentData: [],
      activitiesData: null,
      timelinedata: [],
      productData: null,
      callData: null,
      contactsData: [],
      contactsHeader: [],
      noRecordForTab: false,
      isShowNoteDialogue: null,
      dataToConvertDeal: {},
      noteText: "",
      DealName: "--",
      Acctab: "--",
      SortBy: "",
      SortExp: "",
      PageNumber: 1,
      PageSize: 100,
      DealStatus: "",
      EmailData: [],
      modalItem: null,
      modalTaskItem: null,
      modalFollowUpItem: null,
      modalCallItem: null,
      modalDeleteItem: null,
      isView: false,
      modal: null,
      defaultemailid: null,
      showAdditionalOwnerPopup: false,
      additionalLeadOwners: [],
      stagesData: null,
      salesOrderHeader: null,
      additionalOwnerPermission: [],
      chkpermission: false,
      isShowNotePopup: false,
      expirydate: "",
      Count: 0,
      clienFirstName: "",
      clientLastName: "",
      clientaddr1: "",
      clientaddr2: "",
      clientcity: "",
      clientstate: "",
      clientzip: "",
      clientcompany: "",
      userid: null,
      companyId: null,
      containername:null,
      email:null,
      ApiURL: `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
      maxsize:
        "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize')",
      disallowedfiletypes:
        "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize'))",
      NoteListType: 'list',
      editUrl: `/CRM/ManageDeal/${this.$route.params.id}/Yes/CRM/CRM_DEALS`,
      
      EmailInfo: null,
      quotationData:null,
      quotationHeader:null,
      additionalLeadOwnerId: null,
      stageData: null,
      selectedStage: null,
      dealClosingDate: null,
      countClick: 0,
      CurrentOpenTab: 'timeline',
      isSalesOrder:false,
      salesOrderData:null,
      isloadinf:false
    };
  },
  created: function () {
    this.userid= this.GetUserInfo.ID;
    this.companyId= this.GetUserInfo.CompanyId;
  
    this.containername = this.GetUserInfo.ContainerName;
    this.email = this.GetUserInfo.Email;
    var vm = this;
    vm.FetchData();
    setTimeout(function () { vm.SetBootstraptoolTip() }, 500);
  },
  updated() {
    var vm = this;
    setTimeout(function () { vm.setHeaderForSelectedTab() }, 500);
    $("#ancActivityExpandAllCollapseAll").click(function () {
      if ($(this).attr("show") == "0") 
    {$(this).attr("show", "1");
     $("#divconversationbox").find("#accordion").find('.collapse').addClass('show'); }
     else { 
        $(this).attr("show", "0"); 
     $("#divconversationbox").find("#accordion").find('.collapse').removeClass('show')
    }
    });
  },
  methods: {
    PageTourStart: function () {
      startTour(0);
    },
    DisableButton: function () {
      var vm = this;
      vm.isNotes = false;
      vm.isQuotation = false;
      vm.isActivity = false;
      vm.isAttachment = false;
      vm.isContact = false;
      vm.isSendEmail = false;
      vm.isProduct = false;
      vm.isOwner = false;
      vm.isStages = false;
    },
    CollapseExpand: function (IsShow) {
      var vm = this;
      vm.IsShow = !IsShow;
    },
    openCRMProductModel: function () {
            if ((typeof this.checkForAdditionalPermission !== 'undefined') && !this.checkForAdditionalPermission("AddProducts")) {
                alertR(Globalize.localize("additionalOwnerAuthorityWarning"), true, Globalize.localize('Alert'));
            } else {
                this.showProductModal = (this.showProductModal) ? false : true;
            }
        },
    openCRMExistingContactModel: function () {
      var vm = this;
      if ((typeof vm.checkForAdditionalPermission !== 'undefined') && !vm.checkForAdditionalPermission("AddContact")) {
        alertR(vm.$t("additionalOwnerAuthorityWarning"), true, vm.$t('Alert'));
      } else {
        this.showExistingContactModal = (this.showExistingContactModal) ? false : true;
      }
    },
    openShowNotePopup: function () {
      var ths = this;
      if (
        typeof ths.checkForAdditionalPermission !== "undefined" &&
        !ths.checkForAdditionalPermission("AddNote")
      ) {
        ths.ShowAlert(
          ths.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          ths.$t("Alert")
        );
      } else {
        ths.isShowNotePopup = true;
        $("#tab-notes").trigger("click");
        ths.loadData("notes");
      }
    },
    checkPrivilegeDetailsForOwner: function (e, item) {
      var vm = this;
    
        vm.openAddAdditionalOwner(null);
      },
    
    checkForAdditionalPermission: function (TypeCode) {
      var rtrn = true;
      if (
        this.additionalOwnerPermission != null &&
        this.additionalOwnerPermission.length > 0
      ) {
        var prmsn = this.additionalOwnerPermission.filter(function (c) {
          return c.permission_code == TypeCode;
        });
        if (prmsn.length == 0) {
          rtrn = false;
        }
      }
      return rtrn;
    },
    openAddAdditionalOwner: function (dealOwner) {
      var type = "AddAdditionalOwner";
      if (dealOwner != null) {
        this.additionalLeadOwnerId = dealOwner;
        type = "UpdateAdditionalOwner";
      } else {
        this.additionalLeadOwnerId = null;
      }
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission(type)
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.showAdditionalOwnerPopup =
        !this.showAdditionalOwnerPopup ? true : false;
      }
    },
    deleteAdditionalOwner: function (DealOwnerId) {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("DeleteAdditionalOwner")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
         "warning",
          true,
          this.$t("Alert")
        );
      } else {
        var vm = this;
        talygen.confirm(this.$t("ConfirmDelete"), function (result) {
          if (result) {
            var postJSON = {
              ids: DealOwnerId,
              moduleName: "CRM",
              subModuleCode: "CRM_LEADS",
              refCode: "DELETE_VIEW_LEAD_OWNER",
              refId: vm.$route.params.id,
            };
            if(vm.$route.params.subModuleCode=='CRM_DEALS'){
              postJSON = {
              ids: DealOwnerId,
              moduleName: "CRM",
              subModuleCode: "CRM_DEALS",
              refCode: "DELETE_VIEW_DEAL_OWNER",
              refId: vm.$route.params.id,
            };
            }
            var objectPostString = JSON.stringify(postJSON);
            DealDataService.CommonDelete(objectPostString).then(
              function (response) {   
                if (response.data[0].Status == "Success") {
                  vm.ShowAlert(vm.$t("DeletedSuccess", [vm.$t("AdditionalOwner")]),"success", true, vm.$t("Alert"));
                  vm.loadData("additionalDealowners");
                } else {
                  vm.ShowAlert(vm.$t("DeletedError",[vm.$t("AdditionalOwner")]),"failure",true,vm.$t("Alert"));
                }

              }
            );
          }
        });
      }
    },
    ReopenDeal: function () {
      var vm = this;
      if ("$route.params.id" > 0) {
        talygen.confirm(this.$t("AreYouSuretoReopenDeal"), function (result) {
          if (result) {
            var postJSON = {
              ids: "$route.params.id",
            };
            var objectPostString = JSON.stringify(postJSON);
            vm.$http
              .post(
                baseUrl + "api/ReopenDeal",
                { postString: objectPostString },
                {
                  headers: { Authorization: "bearer @tokenid" },
                }
              )
              .then(
                function (response) {
                  response.headers.get("Expires");
                  var box = "";
                  if (response.body[0].Status == "Success") {
                    if (
                      response.body[0].Value != "CONVERTED_TO_CONTRACT" &&
                      response.body[0].Value != "CONVERTED_TO_DEAL"
                    ) {
                      vm.FetchData();
                  
                      box = ShowAlert(
                        vm.$t("DealReopenedSuccessfully"),
                        "success",
                        true,
                        this.$t("Alert")
                      );
                    } else {
                    
                      box = ShowAlert(
                        vm.$t("CantReopenDueToConverted"),
                        "warning",
                        true,
                        this.$t("Alert")
                      );
                    }
                  } else {
                   
                    box = ShowAlert(
                      vm.$t("ReopenDealError"),
                      "failure",
                      true,
                      this.$t("Alert")
                    );
                  }
                  setTimeout(function () {
                    box.modal("hide");
                  }, 4000);

                },
                function (response) {

                }
              );
          }
        });
      }
    },
    GetLocalizedValue: function (fieldName, displayName) {
      if (this.isBlank(displayName)) {
        var chk = this.$t(String(fieldName).toUpperCase());
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        chk = this.$t(String(fieldName));
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        return fieldName;
      } else {
        var chk = this.$t(String(displayName).toUpperCase());
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        chk = this.$t(String(displayName));
        if (chk || /^\s*$/.test(chk)) {
          return chk;
        }
        return displayName;
      }
    },
   
    FilterDDLandNormalValue: function (item) {
      if (item && item.length > 0) {
        try {
          return JSON.parse(item)[0].name;
        } catch (ex) {
          return "";
        }
      } else {
        return "";
      }
    },
    stageStyles(color, check, type) {
      var styleObj = {};
      if (check) {
          if (type == 'border') {
              styleObj = {
                  //border: '2px solid ' + color + ' !important',
                  'border-radius': '100% !important',
                  'z-index': 9
              }
          }
          else if (type == 'circle') {
              styleObj = {
                  background: color,
                  'border': '2px solid ' + color,
                  'color': '#fff'
              }
          }
          else if (type == 'text') {
              styleObj = {
                  'color': '#fff'
              }
          }
      }
      return styleObj;
    },
    updateDealStage(stageItem, selectedStage) {
      var vm = this;
      if (stageItem != null && vm.countClick == 0) {
        if (vm.selectedStage != null && stageItem.STAGE_ID != vm.selectedStage) {
            if (stageItem.DISPLAY_ICON != null && stageItem.DISPLAY_ICON.toString().indexOf('thumb') > -1) {
                vm.countClick++;
                setTimeout(function () { vm.countClick = 0; }, 300);
                var msg =vm.$t("AreYouSureWantToUpdateStage",[vm.$t("SetUpAccount")]);
                vm.confirmR(msg, true, vm.$t("Update"), function () {
                    vm.updateDealStageFinalFunc(stageItem);
                });
            } else {
                vm.updateDealStageFinalFunc(stageItem);
                vm.countClick = 0;
            }
        }
      }
    },
  updateDealStageFinalFunc(stageItem) {
      var vm = this;
      var postJSON = {
        stage: stageItem.STAGE_ID,
        moduleName: "CRM",
        subModuleCode: vm.$route.params.subModuleCode,
        refId: vm.$route.params.id
      };
      var objectPostString = JSON.stringify(postJSON);
      DealDataService.UdpateDealStage(objectPostString).then(function(response){
        var box = "";
        if (response.data[0].Status == "Success") {
         
          box = vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Stage')]), "success", true, vm.$t("Alert"));
          vm.selectedStage = stageItem.STAGE_ID;
          $.each(vm.LeftViewData, $.proxy(function (index, item) {
              $.each(item.LeftViewData, function (index, item1) {
                  if (item1.name == "stage_id") {
                      item1.value = '[{"name":"' + stageItem.STAGE_NAME + '","value":"' + stageItem.STAGE_ID + '","first_value":null,"second_value":null}]';
                  }
              });
          }));
          if (vm.CurrentOpenTab == 'stages') {
              vm.loadData('stages');
          }
        }
        setTimeout(function () {
            box.modal('hide');
        }, 2000);
        hideLoader();
      });
    },
    FetchData: function () {
      var vm = this;
      vm.isLoading=true;
      DealDataService.FormLayoutView(vm.ApiURL).then(function (response) {
        debugger;
        var fullLeftJSON = response.data.LEFTDATA;
        vm.stageData = response.data.STAGEDATA;
        vm.timelinedata = [];
        fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
          if (element.name == "role_title_id") {
            vm.dataToConvertDeal.roletitle =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "home_phone") {
            vm.dataToConvertDeal.home_phone = element.value;
          }
          if (element.name == 'stage_id') {
            vm.selectedStage = vm.FilterDDLValueForID(element.value);
          }
          if (element.name == 'closing_date') {
            vm.dealClosingDate = vm.$options.filters.formatDate(element.value);
          }
          if (element.name == "business_phone") {
            vm.dataToConvertDeal.business_phone = element.value;
          }
          if (element.name == "contact_number") {
            vm.dataToConvertDeal.contact_number = element.value;
          }
          if (element.name == "account_id") {
            vm.dataToConvertDeal.account_id = element.value;
          }
          if (element.name == "channel_id") {
            vm.dataToConvertDeal.Deal_source_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "industry_type_id") {
            vm.dataToConvertDeal.industry_type_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "Deal_country") {
            vm.dataToConvertDeal.Deal_country =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "Deal_state") {
            vm.dataToConvertDeal.Deal_state =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          }
          if (element.name == "external_source_id") {
            vm.dataToConvertDeal.referred_by =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          } else if (element.name == "status_id") {
            vm.DealStatus = vm.FilterDDLandNormalValue(element.value);
          } else if (element.name == "deal_name") {
            vm.clienFirstName = element.value != null ? element.value : "";
          } else if (element.name == "client_last_name") {
            vm.clientLastName = element.value != null ? element.value : "";
          } else if (element.name == "Deal_address1") {
            vm.clientaddr1 = element.value != null ? element.value : "";
          } else if (element.name == "Deal_address2") {
            vm.clientaddr2 = element.value != null ? element.value : "";
          } else if (element.name == "Deal_city") {
            vm.clientcity = element.value != null ? element.value : "";
          } else if (element.name == "Deal_state") {
            vm.clientstate =
              element.value != null ? JSON.parse(element.value)[0].name : "";
          } else if (element.name == "Deal_zipcode") {
            vm.clientzip = element.value != null ? element.value : "";
          } else if (element.name == "Deal_company_name") {
            vm.clientcompany = element.value != null ? element.value : "";
          } else if (element.name == "client_email") {
            vm.defaultemailid = element.value != null ? element.value : "";
          } else if (element.name == "expected_close_date") {
            vm.dataToConvertDeal.closing_date = element.value;
            vm.expirydate = element.value;
          } else if (element.name == "money_value") {
            vm.dataToConvertDeal.ammount = element.value;
          } else if (element.name == "campaign_id") {
            vm.dataToConvertDeal.campaign_id =
              JSON.parse(element.value) == null
                ? ""
                : JSON.parse(element.value)[0].value;
          } else if (element.name == "Deal_owner_id") {
            vm.dataToConvertDeal.Deal_owner = element.value;
          }
          if (
            element.name == "commission_type" &&
            $.trim(element.value) != ""
          ) {
            element.form_field_visibility = "YES";
            element.display_order = 20;
          }
          if (element.name == "agreement_type" && $.trim(element.value) != "") {
            element.form_field_visibility = "YES";
            element.display_order = 19;
          }
          return element.form_field_visibility == "YES";
        });
        if (vm.DealName == "--") {
          vm.DealName = vm.clienFirstName + " " + vm.clientLastName;
        }
        if (vm.Acctab == "--") {
          vm.Acctab =
             vm.clienFirstName 
            // " " +
            // vm.clientLastName +
            // " " +
            // vm.clientcompany +
            // " " +
            // vm.clientaddr1 +
            // " " +
            // vm.clientaddr2 +
            // " " +
            // vm.clientcity +
            // " " +
            // (vm.clientstate == "Select" ? " " : vm.clientstate) +
            // " " +
            // vm.clientzip;
        }
        vm.dataToConvertDeal.deal_name = vm.DealName;
        if (
          response.data.RIGHTDATA != null &&
          response.data.RIGHTDATA.length > 0
        ) {
          var fullJSON = response.data.RIGHTDATA;
          var gruopBy = "";
          $.each(
            fullJSON,
            $.proxy(function (index, item) {
              gruopBy =vm.$options.filters.formatDate(item.Complete_datetime);
              var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                return element.groupKey == gruopBy;
              });
              if (chkGroup.length == 0) {
                var returnedData = $.grep(fullJSON, function (element, index) {
                  return vm.$options.filters.formatDate(element.Complete_datetime)== gruopBy;
                });
                if (returnedData.length > 0) {
                  var objGroup = {
                    groupKey: gruopBy,
                    groupData: returnedData,
                  };
                  vm.timelinedata.push(objGroup);
                }
              }
            }, this)
          );
          
          vm.noRecordForTab = false;
        } else {
          vm.noRecordForTab = true;
        }
        if (fullLeftJSON != null && fullLeftJSON.length > 0) {
          var groupId = fullLeftJSON[0].group_id;
          $.each(
            fullLeftJSON,
            $.proxy(function (index, item) {
              var chkGroup = $.grep(vm.LeftViewData, function (element, index) {
                return element.group_id == groupId;
              });
              if (chkGroup.length == 0) {
                var returnedData = $.grep(
                  fullLeftJSON,
                  function (element, index) {
                    return element.group_id == groupId;
                  }
                );
                returnedData = returnedData.sort(function (a, b) {
                  var a1 = a.display_order,
                    b1 = b.display_order;
                  if (a1 == b1) return 0;
                  return a1 > b1 ? 1 : -1;
                });
                if (returnedData.length > 0) {
                  var objGroup = {
                    group_id: groupId,
                    group_name: returnedData[0].group_name,
                    LeftViewData: returnedData,
                  };
                  vm.LeftViewData.push(objGroup);
                }
              }
              groupId = item.group_id;
            }, this)
          );
      
        }
        vm.isLoading=false;
        vm.additionalOwnerPermission = response.data.PERMISSION_DATA;
        setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
        setTimeout(function () { vm.SetResponsiveTab() }, 500);
        setTimeout(function () { vm.RadioButtonBootstrap() }, 500);
        setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
        vm.isDetailPage = true;
      });
    },
    changeDateForAddNoteUsrEnd: function (value) {
      try {
        var timeFormat = Globalize.culture().calendar.patterns.t;
        timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
        var getDate = new Date(
          moment(String(value)).year(),
          moment(String(value)).month(),
          moment(String(value)).date(),
          moment(String(value)).hours(),
          moment(String(value)).minutes()
        );
        if (userTimeZone[0].indexOf("+") >= 0) {
          getDate = new Date(
            getDate.setHours(
              getDate.getHours() - parseInt(userTimeZone[0].replace("+", ""))
            )
          );
          getDate = new Date(
            getDate.setMinutes(getDate.getMinutes() - parseInt(userTimeZone[1]))
          );
        } else if (userTimeZone[0].indexOf("-") >= 0) {
          getDate = new Date(
            getDate.setHours(
              getDate.getHours() + parseInt(userTimeZone[0].replace("-", ""))
            )
          );
          getDate = new Date(
            getDate.setMinutes(getDate.getMinutes() + parseInt(userTimeZone[1]))
          );
        }
        return moment(getDate, "YYYY-MM-DD HH:mm").format(
          String(Globalize.culture().calendar.patterns.d).toUpperCase() +
          " " +
          String(timeFormat)
        );
      } catch (e) {
        var timeFormat = Globalize.culture().calendar.patterns.t;
        timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
        return moment(String(value)).format(
          String(Globalize.culture().calendar.patterns.d).toUpperCase() +
          " " +
          String(timeFormat)
        );
      }
    },
    DeleteActivityData: function (id) {
      var vm = this;
      if (id > 0) {
        talygen.confirm(vm.$t("ConfirmDelete"), function(result){
            var postJSON = {
              ids: id,
              moduleName: "CRM",
              subModuleCode: "CRM_DEALS",
              refCode: "DELETE_VIEW_ACTIVITY",
              refId: vm.sourceId
            };
            var objectPostString = JSON.stringify(postJSON);
            DealDataService.CommonDelete(objectPostString).then(
              function (response) {
                if (response.data[0].Status == "Success") {
                  vm.ShowAlert(vm.$t('DeletedSuccess',[vm.$t('Deal')]),"Success",true, vm.$t("Alert"));
                  vm.loadData("activities");
                } else {
                  vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Deal')]), "failure", true, vm.$t("Alert"));
                }                 
              })
            });
        }      
    },
    openCRMAttachmentModal: function () {
      if ((typeof this.checkForAdditionalPermission !== 'undefined') && !this.checkForAdditionalPermission("AddAttachment")) {
        alertR(this.$t("additionalOwnerAuthorityWarning"),'warning', true, this.$t('Alert'));
      } else {

        this.showAttachmentModal = (this.showAttachmentModal) ? false : true;
      }
    },
    openAttachmentDialogue: function () {
      this.isShowNoteDialogue = this.isShowNoteDialogue ? false : true;
    },
    openConvertDealModel: function () {
      var vm = this;
      if (vm.GetPrivilege("/CRM/ConvertDeal", true, false)) {
      } else {
        e.preventDefault();
      }
      talygen.confirm(this.$t("AreyousuretoConvertDeal"), function (result) {
        if (result) {
          vm.showConvertDeal = vm.showConvertDeal ? false : true;
        }
      });
    },
    openCRMEmailModal: function (item) {
      var type = 'AddEmail';
      if (item != null) {
        type = 'UpdateEmail';
      }
      if ((typeof this.checkForAdditionalPermission !== 'undefined') && !this.checkForAdditionalPermission(type)) {
        alertR(this.$t("additionalOwnerAuthorityWarning"), true, this.$t('Alert'));
      } else {
        this.sendEmailModal = (this.sendEmailModal) ? false : true;
        this.EmailInfo = item;
        //editid = item;
      }
    },
    openAddTaskModel: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.showAddTaskModal = this.showAddTaskModal ? false : true;
      }
    },
    openAddNewEvent: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.showAddEventModal = this.showAddEventModal ? false : true;
      }
    },
    openCallEvent: function () {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("AddAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.showCallModal = this.showCallModal ? false : true;
      }
    },
    openEmailModal: function (item) {
      this.sendEmailModal = true;
      this.modalItem = item;
    },
    openActivityModal: function (item) {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("UpdateAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        if (item.activity_type == "Task") {
          this.showAddTaskModal = true;
          this.modalTaskItem = item;
        } else if (item.activity_type == "FollowUp") {
          this.showAddEventModal = true;
          this.modalFollowUpItem = item;
        } else if (item.activity_type == "Call") {
          this.showCallModal = true;
          this.modalCallItem = item;
        }
      }
    },
    DeleteActivity: function (item) {
      if (
        typeof this.checkForAdditionalPermission !== "undefined" &&
        !this.checkForAdditionalPermission("DeleteAcitvities")
      ) {
        this.ShowAlert(
          this.$t("additionalOwnerAuthorityWarning"),
          "warning",
          true,
          this.$t("Alert")
        );
      } else {
        this.modalDeleteItem = item;
        this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
      }
    },
    loadData: function (tabName, callFrom) {
      var vm = this;
      vm.DisableButton();
      vm.isDetailPage = false;
      vm.isSalesOrder=false;
      vm.isAttachment = false;
      vm.isQuotation = false;
      if (tabName == "notes") {
        vm.isNotes = true;
        if (callFrom && callFrom == 'savenote')
          vm.isShowNotePopup = false;
        vm.activeKey = 2;
      } else if (tabName == "attachments") {
        vm.isAttachment = true;
        vm.activeKey = 3;
      } else if (tabName == "activities") {
        vm.activeKey = 4;
        vm.isActivity = true;
      } else if (tabName == "contacts") {
        vm.activeKey = 6;
        vm.isContact = true;
      } else if (tabName == "email") {
        vm.activeKey = 5;
        vm.isSendEmail = true;
      }
      else if (tabName == "products") {
        vm.activeKey = 7;
        vm.isProduct = true;       
      }
      else if (tabName == "salesOrder") {
        vm.isSalesOrder = true;
        vm.activeKey = 9;
      }
      else if (tabName == "stages") {
        vm.activeKey = 10;
        vm.Stages = true;
      }
      else if (tabName == "Calls") {
        vm.Calls = true;
        vm.activeKey = 11;
      }
      else if (tabName == "additionalDealowners") {
        vm.activeKey = 12;
        vm.isOwner = true;
      } else if (tabName == "quotations") {
        vm.isQuotation = true;
      }
      if (tabName != "" && tabName != null) {
        if (tabName == "attachments") {
          DealDataService.AttachmentListing(vm.ApiURL).then(function (
            response
          ) {
            if (response.data != null) {
              vm.attachmentData = response.data.data;
              setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "activities") {
          var vm = this;
          DealDataService.ActivityListing(vm.ApiURL)
            .then(function (response) {
              if (
                response.data != "" &&
                response.data != null &&
                response.data.data.length > 0
              ) {
                vm.activitiesData = response.data.data;
                setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
                setTimeout(function () { vm.ResponsiveDataTable('table-openactivities-content') }, 500);
                setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
                vm.noRecordForTab = false;
              } else {
                setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
                vm.noRecordForTab = true;
              }
            });

        } else if (tabName == "timeline") {
          vm.activeKey = 1;
          vm.isDetailPage = true;
          DealDataService.TimelineListing(vm.ApiURL).then(function (response) {
            if (response.data != null) {
              vm.timelinedata = [];
              var fullJSON = response.data.data;
              var gruopBy = "";
              $.each(
                fullJSON,
                $.proxy(function (index, item) {
                  gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                  var chkGroup = $.grep(
                    vm.timelinedata,
                    function (element, index) {
                      return element.groupKey == gruopBy;
                    }
                  );
                  if (chkGroup.length == 0) {
                    var returnedData = $.grep(
                      fullJSON,
                      function (element, index) {
                        return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                      }
                    );
                    if (returnedData.length > 0) {
                      var objGroup = {
                        groupKey: gruopBy,
                        groupData: returnedData,
                      };
                      vm.timelinedata.push(objGroup);
                    }
                  }
                }, this)
              );
              setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "products") {
          var url = `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;

          DealDataService.getItemListForAssociate(url).then(function (
            response
          ) {

            if (response.data != null && response.data != "") {
              vm.productData = response.data.data;
              setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = true;
              vm.productData = null;
            }
          });
        } else if (tabName == "calls") {
          vm.activeKey = 11;
          url = `id=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}`;
          DealDataService.GetCallLogsListinge(url).then(function (response) {

            if (response.data != null) {
              vm.callData = response.data.data;
              setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = true;
              vm.calltData = null;
            }
          });
        } else if (tabName == "contacts") {
          var url = `id=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
          DealDataService.ContactListing(url).then(function (response) {
            if (response.data != null) {
              vm.contactsData = response.data.data;
              vm.contactsHeader = response.data.schema;
              setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = true;
              vm.contactsData = null;
            }
          });
        } else if (tabName == "email") {
          var url =
            vm.ApiURL +
            vm.SortBy +
            "&sortExp=" +
            vm.SortExp +
            "&pageSize=" +
            vm.PageSize +
            "&pageNumber=" +
            vm.PageNumber;
          DealDataService.EmailListing(this.url).then(function (response) {

            if (response.data != null) {
              vm.EmailData = response.data.data;
              setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "notes") {
          vm.noteData = [];
          DealDataService.NoteListing(vm.ApiURL).then(function (response) {
            if (response.data != null && response.data.data.length > 0) {
              vm.noteData = response.data.data;
              setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "additionalDealowners") {
          var url = `id=${this.$route.params.id}&subModuleCode=CRM_DealS`;
          DealDataService.additionalLeadOwnerListing(url).then(function (
            response
          ) {

            if (response.data != null && response.data.DATA.length > 0) {
              vm.additionalLeadOwners = response.data.DATA;
              setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
              setTimeout(function () { vm.ResponsiveDataTable('table-DealOwner-content') }, 500);
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = true;
            }
          });
        }
        else if (tabName == "salesOrder") {
          debugger;
          url = `subModuleCode=CRM_SALES_ORDERS&sortBy=${vm.SortBy}&pageSize=10&pageNumber=${vm.PageNumber}&dealId=${vm.$route.params.id}`;
          DealDataService.salesOrderListing(url).then(function (
            response
          ) {

            if (response.data != null && response.data != "" && response.data.data.length > 0) {
              vm.salesOrderData = response.data.data;
              vm.salesOrderHeader = response.data.schema;
              setTimeout(function () { vm.CheckBoxBootstrap() }, 500);
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              setTimeout(function () { vm.ResponsiveDataTable('table-salesOrder-content') }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = true;
            }
          });
        }
        else if (tabName == "stages") {
          DealDataService.getStageMappListing(vm.ApiURL).then(function (response) {
            debugger;
            if (response.data != null) {
              vm.stagesData = response.data.data;
              setTimeout(function () { vm.ResponsiveDataTable('table-stages-content') }, 500);
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = false;
            } else {
              setTimeout(function () { vm.bindLeftTabEventForSetHeading() }, 500);
              vm.noRecordForTab = true;
            }
          });
        } else if (tabName == "quotations") {
                        url = "subModuleCode=CRM_QUOTES&sortBy=" + vm.SortBy + "&sortExp=" + vm.SortExp + "&pageSize=" + vm.PageSize + "&pageNumber=" + vm.PageNumber + "&dealId="+vm.$route.params.id;
                        DealDataService.QuotesListingWthQueryString(url).then(function (response) {                          
                            if (response.data.data.length > 0 && response.data != null) {
                                vm.quotationData = response.data.data;
                                vm.quotationHeader = response.data.schema;
                                setTimeout(function () { 
                                  vm.CheckBoxBootstrap();
                                  vm.setHeaderForSelectedTab();
                                  vm.bindLeftTabEventForSetHeading();
                                  vm.ResponsiveDataTable('table-quote-content');

                                 }, 500);
                                
                                vm.noRecordForTab = false;
                            } else {
                              setTimeout(function () { 
                                  vm.setHeaderForSelectedTab();
                                  vm.bindLeftTabEventForSetHeading();
                                 }, 500);                                
                                vm.noRecordForTab = true;
                            }
                        });
                        vm.activeKey=8;
                    }
      }
      this.CurrentOpenTab = tabName;
    },
    openCRMContactModal: function (contactDetails) {
      var type = 'AddContact';
      if (contactDetails != null) {
        type = 'UpdateContact';
      }
      if ((typeof this.checkForAdditionalPermission !== 'undefined') && !this.checkForAdditionalPermission(type)) {
        alertR(Globalize.localize("additionalOwnerAuthorityWarning"), true, Globalize.localize('Alert'));
      } else {
        this.iscontactDetails = contactDetails;
        this.showContactModal = (this.showContactModal) ? false : true;
      }
    },
  },
};
</script>
<template>
  <div id="divMainFormData" v-cloak>
    
    <section id="main-content" class="main-content">
      <loader :is-visible="isLoading"></loader>
      <div class="col-md-12 mt-2 bg_outerdark">
          <div class="wizard">
              <div class="wizard-inner">
                  <ul class="nav nav-tabs">
                    <li v-bind:class="{'nav-item text-center stepper col':true, 'active':(item.STAGE_ID==selectedStage)}" v-for="item in stageData">
                        <a href="javascript:;" v-bind:class="{'nav-link border-0':true}" v-bind:style="stageStyles(item.COLOR_CODE, (item.STAGE_ID==selectedStage), 'border')" v-on:click="updateDealStage(item,selectedStage)">
                            <span class="round-tab" v-bind:class="{'default': item.DISPLAY_ICON==null}" v-bind:style="stageStyles(item.COLOR_CODE, (item.STAGE_ID==selectedStage), 'circle')">
                                <em v-if="item.STAGE_ID==selectedStage && item.DISPLAY_ICON==null" class="fa fa-check" v-bind:style="stageStyles(item.COLOR_CODE, (item.STAGE_ID==selectedStage), 'text')"></em>
                                <em v-if="item.STAGE_ID==selectedStage && item.DISPLAY_ICON!=null" v-bind:class="'fa '+String(item.DISPLAY_ICON).toLowerCase()" v-bind:style="stageStyles(item.COLOR_CODE, (item.STAGE_ID==selectedStage), 'text')"></em>
                                <em v-if="item.STAGE_ID!=selectedStage && item.DISPLAY_ICON!=null" v-bind:class="'fa '+String(item.DISPLAY_ICON).toLowerCase()" v-bind:style="stageStyles(item.COLOR_CODE, (item.STAGE_ID==selectedStage), 'text')"></em>
                            </span>
                        </a>
                        <span class="text">{{item.STAGE_NAME}}</span>
                      </li>
                  </ul>
              </div>
              <div class="rightdatebase text-center">
                  <div style="margin-top:8px;">
                      <label class="bold">{{$t('CLOSING_DATE')}}</label><br /> <span>{{dealClosingDate}}</span>
                  </div>
              </div>
          </div>
      </div>
      <div class="left-menu-tab div-click-event hr_jobpost_a">
        <div class="col-md-12 p-0">
          <div class="theme-primary partition">
            <span class="p-name text-white textelipsis">{{$t('Deal')}}</span>
          </div>
        </div>
        <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="myTab" role="tablist">
          <li class="nav-item">
            <a class="nav-link left-menu" :class="{'active':activeKey==1}" data-bs-target="#tab-Deal-content"
              href="javascript:void(0)" data-bs-toggle="tab" role="tab" id="tab-Details"
              v-on:click="loadData('timeline');DisableButton()">
              <em class="fa fa-list" aria-hidden="true" aria-selected="true"></em>
              {{$t('Detail')}}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewNotes')">
            <a class="nav-link left-menu" :class="{'active':activeKey==2}" data-bs-target="#tab-notes-content"
              href="javascript:void(0)" id="tab-notes" data-bs-toggle="tab" role="tab" v-on:click="loadData('notes')">
              <em class="fa fa-file-text" aria-hidden="true" aria-selected="false"></em>
              {{$t('NOTE')}}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewAttachment')">
            <a class="nav-link left-menu" :class="{'active':activeKey==3}" data-bs-target="#tab-attachment-content"
              href="javascript:void(0)" id="tab-attachment" data-bs-toggle="tab" role="tab"
              v-on:click="loadData('attachments')">
              <em class="fa fa-file" aria-hidden="true" aria-selected="false"></em>
              {{$t('Attachments')}}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewAcitvities')">
            <a class="nav-link left-menu" :class="{'active':activeKey==4}" data-bs-target="#tab-openactivities-content"
              href="javascript:void(0)" id="tab-openactivities" data-bs-toggle="tab" role="tab"
              v-on:click="loadData('activities')" aria-selected="false">
              <em class="fa fa-calendar" aria-hidden="true"></em>
              {{$t('Activities')}}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewEmail')">
            <a class="nav-link left-menu" :class="{'active':activeKey==5}" href="javascript:void(0)" id="tab-emails"
              data-bs-toggle="tab" role="tab" v-on:click="loadData('email')" aria-selected="false">
              <em class="fa fa-envelope" aria-hidden="true"></em>
              {{$t('Emails')}}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewContact')">
            <a class="nav-link left-menu" :class="{'active':activeKey==6}" href="javascript:void(0)" id="tab-Contact"
              data-bs-toggle="tab" role="tab" v-on:click="loadData('contacts')" aria-selected="false">
              <em class="fa fa-users" aria-hidden="true"></em>
              {{$t('Contacts')}}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewProducts')">
            <a class="nav-link left-menu" :class="{'active':activeKey==7}" href="javascript:void(0)" id="tab-products"
              data-bs-toggle="tab" role="tab" v-on:click="loadData('products')">
              <em class="fa fa-shopping-cart" aria-hidden="true" aria-selected="false"></em>
              {{$t('Products')}}
            </a>
          </li>
          <li class="nav-item">
            <a class="nav-link left-menu" href="javascript:void(0)" :class="{'active':activeKey==8}" id="tab-quotation"   data-bs-toggle="tab" role="tab"
              v-on:click="loadData('quotations')"><em class="fa fa-calculator" aria-hidden="true" aria-selected="false"></em>
              {{$t('Quotations')}}</a>
          </li>
          <li class="nav-item">
            <a class="nav-link left-menu" :class="{'active':activeKey==9}" href="javascript:void(0)" id="tab-salesOrder"
              data-toggle="tab" role="tab" v-on:click="loadData('salesOrder')"> <em class="fa fa-balance-scale"
                aria-hidden="true" aria-selected="false"></em>{{$t('Sales')}}
              {{$t('Order')}}</a>
          </li>
          <li class="nav-item">
            <a class="nav-link left-menu" :class="{'active':activeKey==10}" href="javascript:void(0)" id="tab-stages"
              data-toggle="tab" role="tab" v-on:click="loadData('stages')"><em class="fa fa-stack-exchange"
                aria-hidden="true" aria-selected="false"></em>{{$t('Stages')}}</a>
          </li>
          <li class="nav-item">
            <a class="nav-link left-menu" :class="{'active':activeKey==11}" href="javascript:void(0)" id="tab-Calls"
              data-bs-toggle="tab" role="tab" v-on:click="loadData('Calls')">
              <em class="fa fa-phone-square" aria-hidden="true" aria-selected="false"></em>
              {{$t('Call')}} {{$t('Details')}}
            </a>
          </li>
          <li class="nav-item" v-if="checkForAdditionalPermission('ViewAdditionalOwner')">
            <a class="nav-link left-menu" :class="{'active':activeKey==12}" href="javascript:void(0)" id="tab-CoOwner"
              data-bs-toggle="tab" role="tab" v-on:click="loadData('additionalDealowners')">
              <em class="fa fa-user-plus" aria-hidden="true" aria-selected="false"></em>
              {{$t('AdditionalOwners')}}
            </a>
          </li>
        </ul>
      </div>
      <div class="right-content hr_jobpost">
        <div class="col-md-12 p-0 mb-2">
          <div class="theme-primary partition p-actions-expand">
            <span class="p-name text-white" id="span_ForSelectedTabName"></span>
            <input type="hidden" id="hdnActionName" v-bind:value="Acctab" />
            <span class="p-actions float-right">

              <RouterLink :to="'/CRM/DealListing'" class="p-action-btn text-white float-right">
                <em class="fa fa-angle-double-left"></em>
                <br />
                {{$t('BacktoList')}}
              </RouterLink>
              <a href="javascript:;" v-bind:class="{'p-action-btn text-white':true, 'disabled':false}" title
                data-toggle-tooltip="tooltip" data-original-title="Card View" v-on:click="NoteListType = 'card'"
                v-if="isNotes && NoteListType == 'list'">
                <em class="fa fa-th-large"></em>
                <br />
                {{$t('CardView')}}
              </a>
              <a href="javascript:;" v-bind:class="{'p-action-btn text-white':true, 'disabled':false}" title
                data-toggle-tooltip="tooltip" data-original-title="List View" v-on:click="NoteListType = 'list'"
                v-if="isNotes && NoteListType != 'list'">
                <em class="fa fa-list"></em>
                <br />
                {{$t('ListView')}}
              </a>
              <a href="javascript:;" v-bind:class="{'p-action-btn text-white':true, 'disabled':false}" title
                id="ancaddnotesloaddata" data-toggle-tooltip="tooltip" data-original-title="Add Notes"
                v-on:click="openShowNotePopup()" v-if="isNotes">
                <em class="fa fa-plus"></em>
